._3fTIQ {
  font-family: 'Merriweather', serif;
  border-collapse: collapse;
  width: 100%;

}

._3fTIQ td {
    position: relative;
}

._3fTIQ  td, ._3fTIQ  th {
    border: 1px solid #ddd;
    padding: 15px;
    font-family: 'Merriweather', serif;
}

._3fTIQ tr {
    background-color: #f2f3f4;
    color: #79818f;
}

._3fTIQ  tr:nth-child(even){
    background-color: #bcc0c7;
    color: #f2f3f4;
}

/*   
.itemTable  tr:hover {
    background-color: #ddd;

} */

._3fTIQ  th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    color: white;
    font-family: 'Merriweather', serif;
    font-weight: 200;

    background: rgb(76,87,107);
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(76,87,107,1)), to(rgba(174,189,208,1)));
    background: linear-gradient(0deg, rgba(76,87,107,1) 0%, rgba(174,189,208,1) 100%);
}

._2MHXq{
    position: absolute;
    right: 15px;
    top: 13px;
    font-size: 25px;
    cursor: pointer;
}

._2MHXq:hover{
    color: #9cadc2;
}
